.navbar-default {
	position: fixed;
	width: 100%;
	z-index: 99;
    background-color: #fff;
    border-color: transparent;
    padding:0 30px;
    margin-bottom: 0;
    min-height: auto;
    @include rwd(phone){
    	padding: 0 20px;
    }
}
.navbar-brand {
    height: auto;
    @include rwd(phone){
    	padding: 8px;
    	width: 100px;
    }
}
header {
	position: relative;
	width: 100%;
	height: 100vh;
	.mainbg { 
		background-image: url(../img/mainbg.png) ;
		background-position: center center;
		background-repeat:  no-repeat;
		// background-attachment: fixed;
		background-size:  cover;
		width: 100%;
		height: 100vh;
		position:fixed;
		float: left;
		z-index: 0;
		// background-color: #000;
		@include rwd(phone){
			background-position: center bottom;
			background-size: 1100px 550px;
		}
		.mainlogo{
			position:fixed;
			width: 100%;
			top: calc( 50% - 150px);
			left: 0;
			right: 0;
			margin: auto;
			z-index: 1;
			@include rwd(phone){
				top: calc( 50% - 120px);
			}
			img{
				width: 300px;
				margin:auto;
				@include rwd(phone) {
					width: 175px;
				}
			}

		}
		.descript{
			font-family: 'Quicksand', sans-serif;
			font-size:35px;
			letter-spacing: 3px;
		}
	}
}
article{
	position:relative;
	float:left;
	width: 100vw;
}
section.main{
	position:relative;
	padding: 50px 0;
	width: 100%;
	float:left;
	@include rwd(phone) {
		display:flex;
		align-items:center;
		min-height: 100vh;
		justify-content:center;
	}
}
.primary{
	.col-sm-6{
		&:nth-of-type(1){
			@include rwd(phone){
				margin-bottom: 20px;
			}
		}
	}
	h2{
		font-size: 2.5em;
		letter-spacing:2px;
		margin-bottom: 20px;
		@include rwd(phone){
			margin-bottom: 10px;
		}
	}
	p{
	    font-size: 1.2em;
	    letter-spacing: .5px;
	    line-height: 30px;
	    width: 200px;
	    text-align: left;
		margin: auto;
		img{
			float:right;
			width: 40px;
		}
	}
}
.secondary{
	@include rwd(phone){
		.container{
			width: 100%;
		}
	}
	h2{
		margin-top: 0;
		margin-bottom: 20px;
		color:#373737;
		@include rwd(phone){
			margin-bottom: 30px;
		}
	}
}
.wrap{
	position:relative;
	float:left;
	width: 100%;

	&:after{
		@include rwd(nophone){
			content:'';
			position: absolute;
			top:0;
			bottom: 0;
			left: 0;
			right: 0;
			margin:auto;
			width: 95%;
			height: 6px;
			background-color: #fff;
		}
	}
	.col3{
		// padding-bottom: 20%;
		margin-left: 2.5%;
		margin-right: 2.5%;
		z-index: 2;
		transition:border .5s;
		@include grid(5,0);
		@include circle{
		}
		@media (min-width:768px){
			&:after,&:before{
				content:'';
				position:absolute;
				right:0;
				left:0;
				top: 0;
				bottom: 0;
				margin: auto;
				width: 100%;
				padding-bottom: 100%;
				transform:scale(1);
				z-index: 0;
				@include circle{}
				transition:all .3s;
			}
			
			&:hover{
				&:nth-of-type(odd){
					&:after{
						background-color: $primary_yello;
					}	
				}
				&:nth-of-type(even){
					&:after{
						background-color: white;
					}	
				}
				&:after{
					transform:scale(1.1	);
					
					z-index: 0;
				}
				&:before{
					transform:scale(1.02);
					background-color: $primary_blue;
					z-index: 1;
				}
				.circlepic{
					transform:scale(.95);
				}
			}
		}
		.circlepic{
			background-size: 50%;
			border-radius:50%;
			background-position: center 15px;
			background-repeat: no-repeat;
			width: 100%;
			padding-bottom: 100%;
			position: relative;
			float: left;
			z-index:2;
			transition:all .3s;
			
		}
		@include rwd(phone) {
			@include grid(2,15);
			margin-left: 0;
			margin-right: 0;
			margin-bottom: 15px;
			margin-top: 15px;
			.circlepic{
				margin:auto;
				float:none;
				width: 100%;
				padding-bottom: 100%;
			}
		}
	
		&:nth-of-type(odd){
			.circlepic{
				background-color: $primary_yello;
			}
		}
		&:nth-of-type(even){
			.circlepic{
				background-color: white;
			}
		}
	}
	.serDes{
		position: absolute;
		z-index: 3;
		margin: auto;
		left: 0;
		right: 0;
		text-align: center;
		bottom: 20px;
		@include rwd(pad){
			bottom: 10px;
			p{
				margin: 0;
			}
		}
		@include rwd(phone) {
			bottom: 5px;
			p:nth-of-type(2){
				display:none;
				margin: 0;
			}
		}
	}
}
.third{
	position:relative;
	float:left;
	z-index: 1;
	width: 100%;
	height: 100vh;
	display: flex;
	align-items:center;
	justify-content:center;
	.thirdbg{
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		background-image:url(../img/section3bg.png);
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		z-index:-1;
		&:after{
			content: '';
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
			background-image: radial-gradient(0 0, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.5) 100%);
			z-index: 0;
		}

	}
	h2{
		font-size: 36px;
		font-weight: bold;
		p{
			font-size: 24px;
			font-weight: 400;
			margin-top: 50px;
			margin-bottom: 50px;
			@include rwd(phone){
			    font-size: 18px;
			    letter-spacing: .5px;
			    padding: 0px 15px;
			    line-height: 26px;
			}
		}
		button{
			position:relative;
			font-size:28px;
			color: #525252;
			text-shadow: 1px 1px 5px #fff;
			// box-shadow: inset 0px 0px 10px .5px #000;
			background-image: linear-gradient(180deg, hsla(45, 80%, 70%, 1) 0%, hsla(45, 100%, 70%, 1.0) 20%, hsla(45, 100%, 70%, 1.0) 40%,hsla(45, 100%, 65%, 1.0) 60%, hsla(45, 100%, 60%, 1.0) 80%, hsla(45, 100%, 65%, 1.0) 100%);;
			background-size: 100% 100%;
			background-position: left top;
			background-repeat:no-repeat;
			border-radius: 50px;
		    width: 200px;
			height: 59px;
			border:0px solid transparent;
			box-shadow: 0 0 30px rgba(140,100,15,.7) inset,
						0 0 10px rgba(37,20,8,.5) inset,
						0 -3px 10px rgba(37,20,8,.5) inset,
						0 0 3px rgba(0,0,0,.3);

		}
	}
	@include rwd(phone){
		height: 100vh;
		.thirdbg{
			height: 100vh;
		}
	}
}
.forth{
	clear: both;
	padding: 50px 0;
	background-color: #fff;
	@include rwd(phone) {
		display:flex;
		align-items:center;
		height: 100vh;
		justify-content:center;
	}
	h2{
		color:#fecb01;
		text-align: center;
		text-transform: uppercase;
		margin-bottom: 40px;
	}
	.col-sm-4{
		img{
			border-width: 4px 0 4px 0;
			border-style: solid;
			border-color:$primary_yello transparent $primary_yello transparent;
		}
	}
	@include rwd(phone){
		h2{
			margin-bottom: 40px;
		}
		.col-sm-4{
			@include grid(3,5);
			img{
				border-width: 2px 0 2px 0;
			}
		}

	}
}
.fifth{
	background-color: #F93E51;
	padding: 50px 0;
	@include rwd(phone) {
		display:flex;
		align-items:center;
		min-height: 100vh;
		justify-content:center;
		.col-xs-7{
			margin-bottom: 20px;
		}
	}
	h2{
		text-shadow: -2px 2px 1px #D63546;
		margin-bottom: 40px;
	}
	.dollar{
		font-size:34px;
		text-shadow: -2px 2px 1px #D63546;
	}
	.text{
		font-size:19px;
		text-shadow: -1px 1px 0px #D63546;
	}
}
footer{
	position: relative;
	float:left;
	width: 100%;
	background-color: $primary_black;
	padding:15px 0;
	.copyright{
		color:#fff;
		font-size:16px;
		display:flex;
		align-items:center;
		margin-bottom: 0;
		span{
			font-family: 'Quicksand', sans-serif;
			margin-right: 10px;
		}
		img{
			width: 150px;
			margin-right: 20px;
		}

	}
	@include rwd(phone){
		.container{
			padding-left: 0;
			padding-right: 0;
		}
		padding:10px;
		.copyright{
			// float:right;
			text-align: right;
			font-size:12px;
			img{
				float:left;
				width: auto;
				height: 20px;
				margin-right: 5px;
				margin-bottom: 0px;
			}
			display:block;
			span{
				margin-right: 5px;
				display:inline-block
			}
		}
	}
}