@for $i from 0 through 12 {
    .mb#{$i*5} {
        margin-bottom: 5px * $i;
    }
    .mt#{$i*5} {
        margin-top: 5px * $i;
    }
    .pt#{$i*5} {
        padding-top: 5px * $i;
    }
    .font#{$i*2+12}{
    	font-size: ($i*2+12)px;
    }
}
@for $i from 0 through 8 {
 .fa-#{$i}{
 	font-size: $i+em;
 }
}
@for $i from 0 through 9{
    .white_f#{$i}{
        background-color: #f+$i+f+$i+f+$i;
    }
}