
*{
	list-style-type: none;
    font-family: "HelveticaNeue", "Helvetica Neue","Microsoft JhengHei", Helvetica;

}
.small{font-size:12px;}
.bold{font-weight: 600;}
.vertical-top{
    vertical-align:top;
}
.vertical-mid{
    vertical-align:middle;
}
.vertical-bot{
    vertical-align:bottom;
}
.fyello{
	color:$primary_yello;
}
.fgrey{
	color:$primary_grey;
}
.fblack{
	color:$primary_black;
}
.fwhite{
    color:white;
}
.bgblue{
	background-color: $primary_blue;
}
.bgwhite{
	background-color: #fff;
}
.bgblue{
	background-color: $primary_blue;
}
.img-rwd{width: 100%;height: auto;display:block}

.img-responsive{
	margin:auto;
}

.form-control{
	border-radius:0;
}
textarea{
	resize:none;
}
.contact-container{
	padding-left: 15px;
	padding-right: 15px;
	margin-right: auto;
	margin-left: auto;
	@media (min-width: 768px){
	    width: 100%;
	}
	@media (min-width: 1200px){
		width: 1170px;
	}
}

.botton_style1{
    border:1px solid #000;
    padding:10px 50px;
    font-size:18px;
    color:#000;
    @include rwd(phone){
        font-size:16px;
        padding:10px 30px;
    }
    &:hover{
        color:#000;
    }
}
a,a:hover{
    text-decoration: none;
}
ul{
	padding-left: 0;	
}
ul.inlineblock{
	li{
		display:inline-block;
	}
}
