@mixin rwd($media) {
    @if $media==phone {
        @media only screen and (max-width: $browser-small) {
            @content;
        }
    }
    @else if $media==nophone {
       @media only screen and (min-width: $browser-small + 1) {
            @content;
       }
    }
    @else if $media==pad {
        @media only screen and (min-width: $browser-small + 1) and (max-width: $browser-large - 1) {
            @content;
        }
    }
    @else if $media==pc {
        @media only screen and (min-width: $browser-large) {
            @content;
        }
    }
}
@mixin same-heigh($wrap){
    @if $wrap==out{
        display:table;
        table-layout: fixed;
        height: 100%;
        width: 100%;
        // vertical-align:middle;

    }
    @elseif $wrap==inner{
        display: table-cell;
        float: none;
        height: 100%;
        // vertical-align:middle;

        @content;
    }
}
@mixin size($widthV,$heightV){
    width:$widthV;
    height:$heightV;
}
@mixin grid($col,$pd){
    width:100%/$col;
    padding-left:$pd+px;
    padding-right: $pd+px;
    position:relative;
    float:left;
    @content;
}
@mixin circle{
    border-radius:50%;
    @content;
}